.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  background-color: #282c34;
  color: #fff;
padding: 50px;
}
ul{
  list-style: none;
}
.center{

  width: 50%;
  margin: auto;
  text-align: center;
}
h1{
text-transform: uppercase;
letter-spacing: 2px;
font-family: 'Courier New', Courier, monospace;
font-size: 45px;
}
ul li{
  border: 1px solid #fff;
  /* display: inline-block; */
 margin-bottom: 20px;
  transition: all 0.5s;
}
ul{
  padding: 0px;
  width: 80%;
  margin: auto;
}
ul li a{
  color: #fff;
font-size: 20px;
text-decoration: none;
transition: all 0.5s;
display: block;
padding: 10px;
}
ul li:hover{
  background-color: #fff;
}
ul li:hover a{
  color: #282c34;
}
h2{
  font-family: 'Courier New', Courier, monospace;
  font-size: 40px;
  text-transform: uppercase;
letter-spacing: 2px;
}

@media only screen and (max-width: 767px) {
  .center{

    width: 100%;
   
  }
  body {
    padding: 20px;
  }
  h1{
    font-size: 35px;
  }
  ul li a{
    font-size: 18px;
  }
}